import moment from 'moment';

export const monthFormater = (date) => {
  let formatStr = '';
  let monthDayArr = moment(date).format('M:D').split(':');
  switch(monthDayArr[0]) {
    case '1': formatStr = '';break;
    case '2': formatStr = '';break;
    case '3': formatStr = '';break;
    case '4': formatStr = '';break;
    case '5': formatStr = '';break;
    case '6': formatStr = '';break;
    case '7': formatStr = '';break;
    case '8': formatStr = '';break;
    case '9': formatStr = '';break;
    case '10': formatStr = '';break;
    case '11': formatStr = '';break;
    case '12': formatStr = '';break;
  }
  formatStr += monthDayArr[1] < 15 ? '' : '';
  return formatStr;
}
